import styled from "styled-components";

import { colors, marginsCss } from "../styleguide";
import { DisplayFontSizesVariants } from "../styleguide/displayFontSizes";

import { AvatarSizes, StyledAvatarProps } from "./Avatar.types";

interface AvatarVariantOptions {
	size: string,
	iconSize: number
	copy: DisplayFontSizesVariants,
}

export const avatarVariantOptions: Record<AvatarSizes, AvatarVariantOptions> = {
	Small: {
		size: "24px",
		iconSize: 16,
		copy: "caption",
	},
	Medium: {
		size: "32px",
		iconSize: 20,
		copy: "bodyCopyBold",
	},
	Large: {
		size: "40px",
		iconSize: 20,
		copy: "bodyCopyBold",
	},
};

export const StyledAvatar = styled.div<StyledAvatarProps>`
	border-radius: 100px;
	${({ size }) => size && `
		width: ${avatarVariantOptions[size].size};
		height: ${avatarVariantOptions[size].size};
	`}
	background-color: ${({ backgroundColor }) => backgroundColor  || colors.lightGray};
	overflow: hidden;

	${marginsCss};
`;

export const ContentWrapper = styled.div<StyledAvatarProps>`
	height: 100%;
	display: flex;
	justify-content: center;
	overflow: hidden;
	align-items: center;
	& > img {
		border-radius: 100px;
		width: 100%;
   		height: 100%;
	}
`;

